import React, { Component } from "react";
import "./QualityBox.css";
import QBSettingsListItem from "./QBSettingsListItem/QBSettingsListItem";
import HD from "../../../images/HD.png";

export default class QualityBox extends Component {
  state = {
    qualityList: false,
  };

  showSettinsgList = () => {
    this.setState({
      qualityList: !this.state.qualityList,
    });
  };

  closeQualityList = () => {
    setTimeout(() => {
      this.setState({
        qualityList: false,
      });
    }, 300);
  };

  render() {
    const { qualityList } = this.state;

    return (
      <div className="Quality_Box">
        <div className="Quality_Box_CurrentSettings">
          <div className="Quality_Box_CurrentSettings_Img">
            <img src={HD} />
          </div>
          <div className="Quality_Box_CurrentSettings_Title">
            Auto HD (HD, 4K, 8K)
          </div>
          <div
            className={
              qualityList === false
                ? "Quality_Box_CurrentSettings_Span"
                : "Quality_Box_CurrentSettings_Span spanListShow"
            }
            onClick={this.showSettinsgList}
          ></div>
        </div>
        <div
          className={
            qualityList
              ? "Quality_Box_SettingsList"
              : "Quality_Box_SettingsList setListHide"
          }
        >
          <div className="QBSettingsList_Header">Set default quality</div>
          <ul className="QBSettingsList_Main">
            {this.props.settings.map((i) => (
              <QBSettingsListItem
                status={i.status}
                id={i.id}
                name={i.name}
                lable={i.lable}
                settings={this.props.settings}
                getOptions={this.props.getOptions}
                closeList={this.closeQualityList}
              />
            ))}
          </ul>
        </div>
      </div>
    );
  }
}
