import React, { Component } from "react";
import "./QBSettingsListItem.css";

export default class QBSettingsListItem extends Component {
  state = {
    quality: [],
  };

  componentDidUpdate() {
    if (this.state.quality !== this.props.settings) {
      this.setState({
        quality: this.props.settings,
      });
    }
  }

  changeStatus = (e) => {
    const id = e.currentTarget.id;
    const opt = [];

    this.state.quality.forEach((i) => {
      if (i.id === +id) {
        i.status = true;
      } else {
        i.status = false;
      }

      opt.push(i);
    });
    this.props.getOptions(opt);
    this.props.closeList();

    window.chrome.tabs.query({}, function (tabs) {
      for (let tab of tabs) {
        if (tab.url.includes("youtube.com")) {
          window.chrome.tabs.reload(tab.id);
          window.close();
        }
      }
    });
  };

  render() {
    const { id, name, status, lable } = this.props;
    return (
      <li
        className={
          status === true
            ? "QBSettingsList_Main_Item active_item"
            : "QBSettingsList_Main_Item"
        }
        key={id}
        status={status.toString()}
        id={id}
        onClick={this.changeStatus}
      >
        <span className="QBSettingsList_Main_Item_SpanCheck"></span>
        <p className="QBSettingsList_Main_Item_Text">{name}</p>

        <span
          className={lable ? "QBSettingsList_Main_Item_Lable" : "hideLable"}
        >
          {lable}
        </span>
      </li>
    );
  }
}
