import React, { Component } from "react";
import "./Main.css";
import VideoOptionsBox from "./VideoOptionsBox/VideoOptionsBox";
import QualityBox from "./QualityBox/QualityBox";
import star from "../../images/star.png";

export default class Main extends Component {
  state = {
    settings: {
      currentQuality: "",
      main: {},
      quality: [],
    },
  };

  componentDidMount() {
    const _this = this;
    window.chrome.storage.sync.get((d) => {
      if (d.hasOwnProperty("defaultSettingsFromAHD")) {
        _this.setState({
          settings: d.defaultSettingsFromAHD,
        });
      }
    });
  }

  editSettingsMain = (opt) => {
    const newSettings = {
      main: opt,
      quality: this.state.settings.quality,
    };
    this.setNewSettingsToStorage(newSettings);
  };

  editSettingsQuality = (opt) => {
    const activeQuality = opt.filter((i) => i.status === true);
    if (activeQuality[0].name !== this.state.currentQuality) {
      this.setState({ currentQuality: activeQuality[0].name });
      const newSettings = {
        main: this.state.settings.main,
        quality: opt,
      };
      this.setNewSettingsToStorage(newSettings);
    } else {
      return;
    }
  };

  setNewSettingsToStorage = (options) => {
    this.setState({
      settings: options,
    });

    window.chrome.storage.sync.set(
      {
        defaultSettingsFromAHD: options,
      },
      function () {
        window.chrome.runtime.sendMessage({
          action: "updateConfig",
        });
      }
    );
  };

  rateUs = () => {
    window.chrome.tabs.create({
      url: "https://chrome.google.com/webstore/detail/auto-hd-8k4k/lagdcjmbchphhndlbpfajelapcodekll",
    });
  };

  render() {
    return (
      <div className="Main">
        <QualityBox
          settings={this.state.settings.quality}
          getOptions={this.editSettingsQuality}
        />
        <VideoOptionsBox
          settings={this.state.settings.main}
          getOptions={this.editSettingsMain}
        />
        <div className="RateBtn" onClick={this.rateUs}>
          <img src={star} />
          <p className="RateBtn_Text">Please, rate us!</p>
        </div>
      </div>
    );
  }
}
