import React, { Component } from "react";
import "./VideoOptionsBox.css";

export default class VideoOptionsBox extends Component {
  state = {
    main: {},
  };

  componentDidUpdate() {
    if (this.state.main !== this.props.settings) {
      this.setState({
        main: this.props.settings,
      });
    }
  }

  changeStatus = (e) => {
    const id = e.target.id;
    const opt = this.state.main;

    for (let i in opt) {
      if (i === id) {
        opt[i] = !opt[i];
      }
    }

    this.props.getOptions(opt);
  };

  render() {
    return (
      <div className="VideoOptions_Box">
        <div className="VideoOptions_Box_Item">
          <div className="VideoOptions_Box_Item_Img">
            <div className="Box_Item_Inner_Img AE"></div>
          </div>
          <div className="VideoOptions_Box_Item_Title">Audio Equalizer</div>
          <label className="VideoOptions_Box_Item_Input">
            <input
              type="checkbox"
              id="AE"
              onChange={this.changeStatus}
              checked={this.state.main["AE"]}
            />
            <span className="slider round"></span>
          </label>
        </div>
        <div className="VideoOptions_Box_Item">
          <div className="VideoOptions_Box_Item_Img">
            <div className="Box_Item_Inner_Img CGS"></div>
          </div>
          <div className="VideoOptions_Box_Item_Title">
            Create GIFs, Screenshots
          </div>
          <label className="VideoOptions_Box_Item_Input">
            <input
              type="checkbox"
              id="CGS"
              onChange={this.changeStatus}
              checked={this.state.main["CGS"]}
            />
            <span className="slider round"></span>
          </label>
        </div>
        <div className="VideoOptions_Box_Item">
          <div className="VideoOptions_Box_Item_Img">
            <div className="Box_Item_Inner_Img FP"></div>
          </div>
          <div className="VideoOptions_Box_Item_Title">Floating Player</div>
          <label className="VideoOptions_Box_Item_Input">
            <input
              type="checkbox"
              id="FP"
              onChange={this.changeStatus}
              checked={this.state.main["FP"]}
            />
            <span className="slider round"></span>
          </label>
        </div>
        <div className="VideoOptions_Box_Item">
          <div className="VideoOptions_Box_Item_Img">
            <div className="Box_Item_Inner_Img CF"></div>
          </div>
          <div className="VideoOptions_Box_Item_Title">Color Filter</div>
          <label className="VideoOptions_Box_Item_Input">
            <input
              type="checkbox"
              id="CF"
              onChange={this.changeStatus}
              checked={this.state.main["CF"]}
            />
            <span className="slider round"></span>
          </label>
        </div>
      </div>
    );
  }
}
